.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #112f6b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#routerMenu{
  text-align: left;
    font-size: larger;
    text-decoration-color: black;
}

.menuLinks:visited {
  color:black;
  text-decoration-color: black;
}

#menuButton{
  background-color: #112f6b54;;
  text-align: center;
  position: absolute;
  top: 30px;
  left: 30px;
  font-size: x-large;
}